.main {
  height: 90px;
  background-color: rgb(248, 245, 245);
  display: flex;
}
@media screen and (max-width: 730px) {
  .left {
    display: none;
  }
  .main {
    max-width: 375px;
  }
  .right {
    display: none;
  }
  .loginBtn {
    display: none;
  }
  .user_notification {
    display: none;
  }
  .center {
    max-width: 440px;
  }
  .nav_link {
    font-size: 12px;
  }
  .img_navbar {
    display: none;
  }
}
.left {
  flex: 3;
  display: flex;
  justify-content: center;
  align-items: center;
}
.right {
  flex: 3;
  display: flex;
  justify-content: center;
  align-items: center;
}
.center {
  flex: 6;
  display: flex;
  align-items: center;
  justify-content: center;
}
img {
  width: 190px;
  height: 70px;
}

.loginBtn {
  border: 1px solid gray;
  background-color: transparent;
  padding: 10px;
  letter-spacing: 0.7px;
}

/* login page */

.login {
  height: calc(100vh - 90px);
  display: flex;
}
.login_left {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.login_right {
  flex: 1;
  display: flex;
  align-items: center;
}
.login_img {
  height: 400px;
  width: 400px;
}

.login_form {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 46%;
  border: 1px solid rgb(219, 219, 219);
  padding: 30px;
}

.btn_login {
  height: 40px;
  width: 250px;
  background-color: #1d96fd;
  border: none;
  margin: 10px;
  color: white;
  letter-spacing: 1px;
}

/* home page */
.home {
  width: 100vw;
}

.home_top {
  height: 64vh;
  background-color: #e7f5f3;
  display: flex;
}
.home_left {
  flex: 6;
  display: flex;
  align-items: center;
  justify-content: center;
}
.home_right {
  flex: 6;
  display: flex;
  align-items: center;
  justify-content: center;
}
.home_img {
  height: 600px;
  width: 900px;
}
.doc_img:hover {
  scale: 1;
}

.news_title {
  text-decoration: none;
  color: black !important;
  margin: 30px;
}
.news_image {
  transition: transform 0.3s;
}

.news_image:hover {
  transform: scale(1.03);
  color: rgb(58, 58, 58);
}
.box_news {
  box-shadow: 0px 1px 10px 1px rgba(128, 128, 128, 0.75);
  -webkit-box-shadow: 0px 1px 10px 1px rgba(128, 128, 128, 0.75);
  -moz-box-shadow: 0px 1px 10px 1px rgba(128, 128, 128, 0.75);
}
.news_component {
  box-shadow: 0px 1px 10px 1px rgba(128, 128, 128, 0.75);
  -webkit-box-shadow: 0px 1px 10px 1px rgba(128, 128, 128, 0.75);
  -moz-box-shadow: 0px 1px 10px 1px rgba(128, 128, 128, 0.75);
}

/* login  responsive */
@media screen and (max-width: 730px) {
  .login_img {
    display: none;
  }
  .login_form {
    width: 90%;
  }
  .login_right {
    width: 370px;
    margin-right: "150px";
  }
  .login {
  }
}
.home_heading {
  font-size: 50px;
  font-weight: 700;
  letter-spacing: 1px;
}

/* our team -> experts */

.main-experts {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  width: 100vw;
}
.our_team {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 50px;
}
.our_team_p {
  font-size: 22px;
  color: gray;
  letter-spacing: 1px;
  font-weight: bold;
}
.our_exp_p {
  letter-spacing: 1px;
  font-size: 40px;
  font-weight: 800;
}
.exp_main {
  display: flex;
  justify-content: center;
  align-items: center;
}
.exp_div {
  display: flex;
  flex-wrap: wrap;
  width: 75%;
}
.map_doc_list {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin: 50px;
}

/* doctor list */
.doc_list_main {
  cursor: pointer;
  width: 320px;
  height: 600px;
}
.doc_img_div {
  flex: 6;
}
.doc_img {
  height: 100%;
  width: 100%;
  border-radius: 50%;
}

.doc_list_content {
  flex: 6;
}
.doc_list_content_p {
  color: gray;
  text-align: center;
  margin-top: 8px;
}

/* home page */
@media screen and (max-width: 730px) {
  .hone_right {
    display: none;
  }
  .home_img {
    display: none;
  }
  .home_left {
  }
  .top_content {
    width: 80%;
  }
  .home_heading {
    font-size: 34px;
  }
  .doctors {
  }
  .our_team {
    display: "flex";
    flex-direction: column;
  }
  .our_exp_p {
    font-size: 30px;
  }
  .doc_img {
    height: 80%;
    width: 80%;
  }
  .doc_list_content_p {
    margin-right: 55px;
    margin-top: 20px;
  }
  .doc_list_main {
  }
  .exp_div {
    width: 100%;
    height: 100%;
  }
  .home_news_heading {
    flex-direction: column;
  }
  .home_news_button {
    margin-right: 60px;
    margin-top: 20px;
  }
  .home_news_com_3 {
    margin-top: 20px;
  }

  .home_news_com_p {
    display: none;
  }
  .footer{
    background-color: #1d96fd;
  }
}

/* home news */

.home_news_main {
  margin-top: 200px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 200px;
}
.home_news_main_2 {
  width: 72%;
}
.home_news_main_3 {
  flex: 3;
  margin-bottom: 80px;
}
.home_news_heading {
  display: flex;
  justify-content: space-between;
}
.home_news_heading_p {
  font-size: 20px;
  color: gray;
  font-weight: 700;
}
.home_news_heading_h {
  font-size: 34px;
  font-weight: 800;
}
.home_news_button {
  float: right;
  background-color: transparent;
  border: 2px solid #14a2b7;
  padding: 10px;
  color: gray;
  width: 170px;
}
.home_news_button_link {
  text-decoration: none;
  color: black;
  font-weight: 600;
  letter-spacing: 1px;
}
.home_news_com {
  flex: 9;
}
.home_news_com_2 {
  height: 300px;
  display: flex;
  margin-top: 20px;
  flex-wrap: wrap;

  width: 100%;
}
.home_news_com_3 {
  height: 270px;
  width: 380px;
  border: 1px solid gray;
  margin: 10px;
  box-shadow: 0px 1px 10px 1px rgba(128, 128, 128, 0.75);
  -webkit-box-shadow: 0px 1px 10px 1px rgba(128, 128, 128, 0.75);
  -moz-box-shadow: 0px 1px 10px 1px rgba(128, 128, 128, 0.75);
}
.home_news_com_h {
  margin: 10px;
  font-size: 18px;
  letter-spacing: 0.7px;
  font-weight: 800;
}
.home_news_com_p {
  margin: 10px;
  color: gray;
  letter-spacing: 0.5px;
  margin-top: 20px;
}
.home_news_com_p_2 {
  color: rgb(58, 58, 58);
  margin-left: 10px;
}
.home_news_com_p_3 {
  color: rgb(58, 58, 58);
  margin-left: 10px;
}

.footer {
  background-color: #121c1d;
  height: 120px;
  display: flex;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}
.footer_main {
  width: 80%;
  height: 80%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.footer_left {
  flex: 3;
}
.footer_center {
  flex: 6;
}
.footer_right {
  flex: 3;
}
.icons {
  width: 280px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
.footer_mail {
  display: flex;
  justify-content: center;
  align-items: center;
}
